import React, { Component } from 'react'
import 'isomorphic-fetch'

import Layout from '../components/layout.js'
import css from '../style.css'
import Category from '../components/Category.js'
const API = 'http://localhost:4000/graphql'

export default class ManagementSystem extends Component {
  static async getInitialProps() {
    const query = `{
      allCategories {
        id
        name
        projects {
          title
          description
          id
        }
      }
    }`
    const res = await fetch(API, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        Accept: 'application/json'
      },
      body: JSON.stringify({ query })
    })

    const { data } = await res.json()

    return data
  }

  constructor() {
    super()
    this.state = {
      categories: [],
      title: '',
      desc: ''
    }
  }

  componentWillMount = () => {
    this.setState({ categories: this.props.allCategories })
  }

  onDrop = (e, targetCategoryId) => {
    const fromCategoryId = e.dataTransfer.getData('fromCategoryId')
    const draggedProject = JSON.parse(e.dataTransfer.getData('draggedProject'))

    const categories = this.state.categories.slice()
    if (targetCategoryId === fromCategoryId) return

    // To category..
    let targetCategory = []
    let targetCategoryIndex = -1
    categories.forEach((category, index) => {
      if (category.id === targetCategoryId) {
        targetCategory = category
        targetCategoryIndex = index
      } else if (fromCategoryId === category.id) {
        category.projects = category.projects.filter(
          project => project.id !== draggedProject.id
        )
      }
    })

    targetCategory.projects.push(draggedProject)
    categories.splice(targetCategoryIndex, 1, targetCategory)
    this.setState({ categories })

    this.updateDB({
      fromCategoryId,
      toCategoryId: targetCategoryId,
      projectId: draggedProject.id
    })
  }

  updateDB = async ({ fromCategoryId, toCategoryId, projectId }) => {
    const query = `
      mutation  MoveProject($input: MoveProjectInput) {
          moveProject(input: $input)
          {
            id
            name
            projects {
              title
              id
            }
          }
         }
    `
    const res = await fetch(API, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        Accept: 'application/json'
      },
      body: JSON.stringify({
        query,
        variables: {
          input: {
            fromCategoryId,
            toCategoryId,
            projectId
          }
        }
      })
    })

    const { data } = await res.json()
  }

  removeProject = async (categoryId, projectId) => {
    const query = `
    mutation  RemoveProject($input: RemoveProjectInput) {
        removeProject(input: $input) {
            title
            id
            description
        }
    }
  `
    const res = await fetch(API, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        Accept: 'application/json'
      },
      body: JSON.stringify({
        query,
        variables: {
          input: {
            id: categoryId,
            projectId
          }
        }
      })
    })

    const { data } = await res.json()
    console.log(data)

    if (data) {
      const categories = this.state.categories.slice()
      categories.forEach((category, index) => {
        if (category.id === categoryId) {
          category.projects = category.projects.filter(
            project => project.id !== projectId
          )
        }
      })
      this.setState({ categories })
    }
  }

  handleChange(event, type) {
    this.setState({ [type]: event.target.value })
  }

  addProject = e => {
    e.preventDefault()
    const { title, desc } = this.state
  }

  render() {
    // console.log('the state:', this.state)
    const { categories } = this.state

    const allCategories = categories.map(category => (
      // <div key={category.id} className={css.item}>
      // </div>
      <Category
        key={category.id}
        category={category}
        onDrop={this.onDrop}
        removeProject={this.removeProject}
      />
    ))

    return (
      <Layout>
        <div className={css.container}>{allCategories}</div>
        <div className="createProject">
          <form onSubmit={this.addProject} className={css.addProject}>
            Title:
            <input
              type="text"
              value={this.state.title}
              onChange={e => this.handleChange(e, 'title')}
            />{' '}
            Description:
            <textarea
              type="text"
              value={this.state.desc}
              onChange={e => this.handleChange(e, 'desc')}
            />
            <input type="submit" value="Submit" />
          </form>
        </div>
      </Layout>
    )
  }
}

